.osc-previous-next-button-block {
  width: 100%;
  height: 74px;
  margin-top: 20px;
  padding: 15px;
  background-color: #f5f5f5;
  overflow: hidden;
}
.osc-previous-next-button-block .osc-previous-button {
  position: relative;
  padding-left: 18px;
  font-weight: 600;
  line-height: 44px;
  color: #004699;
  cursor: pointer;
  float: left;
}
.osc-previous-next-button-block .osc-previous-button:before {
  position: absolute;
  top: 13px;
  left: 0;
  width: 8px;
  height: 14px;
  content: '';
  background-size: 8px 14px;
  background-repeat: no-repeat;
  background-image: url(data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSI4IiBoZWlnaHQ9IjEzIiB2aWV3Qm94PSIwIDAgOCAxMyI+CiAgICA8cGF0aCBmaWxsPSIjMDA0Njk5IiBmaWxsLXJ1bGU9ImV2ZW5vZGQiIGQ9Ik03LjQxIDEuOThMMi44MyA2Ljc2bDQuNTggNC43ODJMNiAxMy4wMSAwIDYuNzYgNiAuNTF6Ii8+Cjwvc3ZnPgo=);
}
.osc-previous-next-button-block .osc-previous-button:hover {
  text-decoration: underline;
}
.osc-previous-next-button-block .osc-next-button {
  position: relative;
  margin-right: 17px;
  padding: 11px 8px 11px 15px;
  font-size: 16px;
  font-weight: 600;
  line-height: 22px;
  color: white;
  background-color: #164995;
  cursor: pointer;
  float: right;
}
.osc-previous-next-button-block .osc-next-button:before {
  position: absolute;
  border-left: 17px solid #164995;
  border-top: 22px solid transparent;
  border-bottom: 22px solid transparent;
  border-right: 0;
  top: 0px;
  right: -17px;
  width: 0px;
  height: 0px;
  content: '';
}
.osc-previous-next-button-block .osc-next-button:hover {
  background-color: #003066;
}
.osc-previous-next-button-block .osc-next-button:hover:before {
  border-left: 17px solid #003066;
}
